import { z } from "zod"
import { bigdataSchemas } from "@rp/common/contracts"
import { generateDate } from "./generateDate"
import { generateId } from "./generateId"

type Props = {
  inputMessage: string
  content: string
}

export const generateChatInteraction = ({
  inputMessage,
  content,
}: Props): z.infer<typeof bigdataSchemas.st_user_chats_ChatInteraction> => {
  const date = generateDate()
  const interactionTimestamp = `${Date.now()}000`
  return {
    inputMessage,
    interactionTimestamp,
    dateCreated: date,
    lastUpdated: date,
    origin: {
      type: "user_message",
      queryId: null,
    },
    originSources: [],
    auditTraces: [],
    response: content,
    responseType: "TEXT" as const,
    responseTimeMs: 10,
    responseId: generateId(),
    responseTimestamp: interactionTimestamp,
  }
}
